<template>
  <div>

    <div style="margin-right: 10px; margin-left: 10px;">

      <div class="boderDes">
        <h4 style="margin-left: 20px;">{{title}}</h4>

        <el-form
          ref="data"
          :inline="true"
          :disabled='formDisable'
          :model="data"
          label-width="90px"
        >

          <el-form-item
            label="线路编号"
            prop="id"
          >
            <el-input
              v-model="data.id"
              :disabled='idDisable'
            ></el-input>
          </el-form-item>
          <el-form-item
            label="线路名称"
            prop="name"
          >
            <el-input v-model="data.name"></el-input>
          </el-form-item>

          <el-form-item
            label="英文名称"
            prop="enName"
          >
            <el-input v-model="data.enName"></el-input>
          </el-form-item>

          <el-form-item
            label="线路数"
            prop="subLineNum"
          >
            <el-input v-model="data.subLineNum"></el-input>
          </el-form-item>

          <el-form-item
            label="数据服务器"
            prop="dataServer"
          >
            <el-input v-model="data.dataServer"></el-input>
          </el-form-item>
          <el-form-item
            label="命令服务器"
            prop="cmdServer"
          >
            <el-input v-model="data.cmdServer"></el-input>
          </el-form-item>

        </el-form>
      </div>
     <btn-group :showAddBtn="false"
     :okState="okSta"
     :isAdd="btnIsAdd"
      @editAction="editClick"
      @cancelAction="cancelClick" 
      @okAction="okClick"></btn-group>

      <div style="margin-top:20px">
        <el-table
          highlight-current-row
          border
          :data="tableData"
          style="width: 100%"
          @cell-dblclick="tableDbEdit"
          :disabled="formDisable"
          max-height="500px"
        >

          <el-table-column
            label="编号"
            prop="id"
            width="100"
          >
          </el-table-column>
          <el-table-column
            label="名称"
            prop="name"
          >
          </el-table-column>
          <el-table-column
            label="英文名称"
            prop="enName"
          >
          </el-table-column>
          <el-table-column
            label="开始时间"
            prop="startTime"
          >
          </el-table-column>
          <el-table-column
            label="停运时间"
            prop="stopTime"
          >
          </el-table-column>

          <el-table-column
            label="数据服务器"
            prop="dataServer"
          >
          </el-table-column>

          <el-table-column
            label="命令服务器"
            prop="cmdServer"
          >
          </el-table-column>

          <el-table-column
            label="车站ATS"
            prop="stationAts"
          >
          </el-table-column>

          <el-table-column
            label="列车ATS"
            prop="trainAts"
          >
          </el-table-column>

          <el-table-column
            label="描述"
            prop="des"
          >
          </el-table-column>
        </el-table>
      </div>

    </div>

  </div>
</template>

<script>
import { Message } from 'element-ui';
import btnGroup from './btnGroup.vue';
export default {
  components: { btnGroup },
  props: {
    code: String,
    isStation: Boolean,
    isAdd:Boolean
  },
  data() {
    return {
      title:'线路信息',
      btnIsAdd:false,
      formDisable: true,
      data: {},
      tableData: [],
      currentFormData: {},
     okSta:false,
     idDisable:true
    };
  },
  watch: {
    data: {
      handler(newvale) {
        console.log(JSON.stringify(newvale));
        console.log(JSON.stringify(this.currentFormData));
        if (JSON.stringify(newvale) === JSON.stringify(this.currentFormData)) {
          this.okSta= true;
          
        } else  {
          this.okSta = false;
        }
      },
      deep: true,
    },
 isAdd(){
    this.updateData();    
 },
    code() {
      this.updateData();
    },
    isStation() {
      this.updateData();
    },
    idDisable(){
       this.updateData();
    }
  },
  methods: {
    tableDbEdit(row, column, cell, event) {
      console.log(JSON.stringify(row));
      console.log(JSON.stringify(column));
      console.log(JSON.stringify(cell));
      if (this.formDisable) {
        return;
      }
      if (cell.cellIndex <= 0) {
        return;
      }

      console.log(cell, event);
      //双击修改
    },
   editClick(callbck){
      this.formDisable = false;
      callbck();
   },
   cancelClick(callbck){
      this.formDisable = true;
      this.data = JSON.parse(JSON.stringify(this.currentFormData));
      callbck();
   },
    
    okClick(callbck) {
      var that = this;

      if(this.isAdd)
      {
        var suc =  this.addLineOk();
        if(suc)
        {
        
          callbck();
        }
        return;
      }

      this.$httpApi
        .updateInfo(this.code,that.data)
        .then(function (response) {

          that.okSta = true;
          if (response.data.code == "0") {
           
           
            that.currentFormData = JSON.parse(JSON.stringify(that.data));
             that.formDisable = true;

            callbck();
            that.$emit('change',that.data.id, that.data.name);
          } else {
         
            that.data = JSON.parse(JSON.stringify(that.currentFormData));
          }
        })
        .catch(function () {
          //alert(err)
          //that.data = JSON.parse(JSON.stringify(that.currentFormData));
        });

     
    },
    addLineOk()
    {
      var that = this;
      console.log('添加线路');

       this.$httpApi
        .addLine(that.data)
        .then(function (response) {

        
          if (response.data.code == "0") {
           
             that.okSta = true;
            that.currentFormData = JSON.parse(JSON.stringify(that.data));
             that.formDisable = true;

            
            that.$emit('add',that.data.id, that.data.name);
            return true;
          } else {
            Message.error(response.data.message);
           // that.data = JSON.parse(JSON.stringify(that.currentFormData));
          }
        });
        return false;
    },
    initAddLine()
    {
       this.btnIsAdd = true;
      this.title = "增加线路信息(编码格式：Lxxx)"
        this.formDisable = false;
        this.idDisable = false;
        this.data = {id:'L',name:'',enName:'',subLineNum:'1',dataServer:'',cmdServer:''};
         this.currentFormData = JSON.parse(JSON.stringify( this.data));
         this.tableData = [];
        console.log("添加");
    },
    updateData() {
      console.log("获取线路信息" + this.code);

      if(this.isAdd)
      {
       this.initAddLine();
        return;
      }
        this.btnIsAdd = false;
       this.title = "线路信息";
      this.idDisable  = true;
      var that = this;
      this.$httpApi.lineInfo(this.code).then(function (response) {
        if (response.data.code == "0") {
          //that.treeData=response.data.dataList;
          console.log(JSON.stringify(response.data));
          that.currentFormData = JSON.parse(JSON.stringify(response.data.info));
         
          that.data = response.data.info;
          that.tableData = response.data.devList;
        }
      });
    },
  },
  mounted() {
    this.updateData();
  },
};
</script>

<style>
.boderDes {
  border: 1px solid lightgray;

  margin-bottom: 20px;
}
</style>