<template>
  <div>

    <el-row :gutter="20">
      <el-col :span="5">
        <div class="fullHeight borderDes">

          <el-tree
            ref="tree"
            :highlight-current="true"
            :data="treeData"
            :props="props"
            :load="loadNode"
            @node-contextmenu="rightClick"
            lazy
            node-key="id"
            :expand-on-click-node="false"
            @node-click="clickFunc"
          >

            <span
              class="custom-tree-node"
              slot-scope="{ node, data }"
            >
              <span>
                 <svg-icon :icon-class="data.icon" class="treeICon"  />{{ data.name }}
              </span>
            </span>

          </el-tree>

        </div>

      </el-col>

      <el-col :span="19">
        <div class="fullHeight">

          <allLineVue v-if="sideShow_allLine"></allLineVue>
          <stationinfoVue
            v-else-if="sideShow_station"
            :code="trsfData.id"
            :isStation="trsfType"
            :isAdd="isAdd"
            @add="addStaAction"
            @change="changeStationName"
          ></stationinfoVue>
          <lineInfoVue
            v-else-if="sideShow_line"
            :code="trsfData.id"
            :isAdd="isAdd"
            @change="changeLineName"
            @add="addLinAction"
          ></lineInfoVue>

        </div>
      </el-col>

      <div
        id="menu"
        class="menu"
        v-show="menuVisible"
      >

        <div class="menu__item">
          <el-button
            type="success"
            icon="el-icon-plus"
            size="mini"
            :disabled="!showAddLine"
            @click="addLine"
          >新增线路</el-button>

        </div>

        <div class="menu__item">
          <el-button
            type="success"
            icon="el-icon-delete"
            size="mini"
            :disabled="!showDeleteLine"
            @click="deleteLinAction"
          >删除线路</el-button>
        </div>

        <div class="menu__item">
          <el-button
            type="warning"
            icon="el-icon-plus"
            size="mini"
            :disabled="!showAddStation"
            @click="addStation(true)"
          >新增车站</el-button>

        </div>

        <div class="menu__item">
          <el-button
            type="warning"
            icon="el-icon-delete"
            size="mini"
            :disabled="!showDeleteStation"
            @click="deleteStation"
          >删除车站</el-button>
        </div>

        <div class="menu__item">
          <el-button
            type="primary"
            icon="el-icon-plus"
            size="mini"
            :disabled="!showAddTrain"
            @click="addStation(false)"
          >新增列车</el-button>

        </div>

        <div
          class="menu__item"
          style="margin-bottom:5px"
        >
          <el-button
            type="primary"
            icon="el-icon-delete"
            size="mini"
            :disabled="!showDeleteTrain"
            @click="deleteStation"
          >删除列车</el-button>
        </div>

      </div>

      <el-dialog
        center
        title="提示"
        :visible.sync="dialog.visible"
        width="20%"
      >
        <span>{{dialog.tipMsg}}</span>
        <span
          slot="footer"
          class="dialog-footer"
        >
          <el-button @click="dialog.visible = false">取 消</el-button>
          <el-button
            type="primary"
            @click="dialogOkClick"
          >确 定</el-button>
        </span>
      </el-dialog>
    </el-row>
  </div>
</template>

<script>
import allLineVue from "./lineCfg/allLine.vue";
import stationinfoVue from "./lineCfg/stationinfo.vue";
import lineInfoVue from "./lineCfg/lineInfo.vue";
import { Message } from "element-ui";
export default {
  components: {
    allLineVue,
    stationinfoVue,
    lineInfoVue,
  },

  data() {
    return {
      node: null,
      treeData: [],
      props: {
        label: "name",
        children: "children",
        isLeaf: "leaf",
        id: "id",
        type: "type",
        parentId: "parentId",
      },
      menuVisible: false,
      showAddLine: false,
      showAddStation: false,
      showDeleteLine: false,
      showDeleteStation: false,
      showDeleteTrain: false,
      showAddTrain: false,
      currentData: null,
      currentNode: null,

      sideShow_allLine: false,
      sideShow_line: false,
      sideShow_station: false,

      trsfData: "",
      trsfType: true,
      isAdd: false,
      dialog: {
        visible: false,
        tipMsg: "",
        isLine: false,
        id: "",
      },
    };
  },
  methods: {
    changeStationName(id, name) {
      this.trsfData.name = id + "  " + name;
    },
    changeLineName(id, name) {
      console.log(id + "  " + name);
      this.trsfData.name = id + "  " + name;
    },
    loadNode(node, resolve) {
      if (node.level === 0) {
        this.node = node;
        return resolve([{ name: "OCC及车站", type: 1, id: "parentID",icon:'occ' }]);
      }
      if (node.level >= 1) {
        console.log(JSON.stringify(node.data));
        this.$httpApi.lineList(node.data.id).then(function (response) {
          if (response.data.code == "0") {
            //that.treeData=response.data.dataList;
            console.log(JSON.stringify(response.data.dataList));
            return resolve(response.data.dataList);
          }
        });

        return resolve([]);
      }
    },
    rightClick(MouseEvent, object, Node) {
      this.menuVisible = false;
      this.currentData = object; // 定义变量接收该节点所对应的对象
      this.$refs.tree.setCurrentKey(object.id);
      this.currentNode = Node; // 定义变量接收该节点对应的 Nod

      console.log("right click level=", Node.level, JSON.stringify(object));
      this.showDeleteLine =
        this.showAddTrain =
        this.showAddStation =
          Node.level != 1 && object.type == 3;
      this.showDeleteStation = Node.level != 1 && object.type == 4;
      this.showDeleteTrain = Node.level != 1 && object.type == 5;

      this.showAddLine = Node.level == 1 || object.type < 4;

      this.menuVisible = true;
      document.addEventListener("click", this.foo); // 监听事件鼠标点击事件，若点击则隐藏菜单

      var menu = document.querySelector("#menu");

      menu.style.left = MouseEvent.clientX - 150 + "px";
      menu.style.top = MouseEvent.clientY - 60 + "px";
      this.clickFunc(object, Node, null);
    },
    foo() {
      this.menuVisible = false;
      document.removeEventListener("click", this.foo);
    },

    clickFunc(trdata) {
   
      console.log("获取信息：" + JSON.stringify(trdata));

      this.sideShow_allLine = trdata.type == 1;
      this.sideShow_line = trdata.type == 3;
      this.sideShow_station =
        trdata.type == 2 || trdata.type == 4 || trdata.type == 5;

      this.trsfData = trdata;
      this.trsfType = trdata.type != 5;
      this.isAdd = false;
    },

    addLine() {
      this.isAdd = true;
      this.sideShow_allLine = this.sideShow_station = false;
      this.sideShow_line = true;
      this.trsfData = this.currentData;
      this.trsfType = false;
    },
    addStaAction(id, name, isTrain) {
      console.log("添加完成站点", id, name);
      var data = {
        id: id,
        type: isTrain ? 5 : 4,
        name: id + " " + name,
        leaf: true,
      };

      var ob = this.$refs.tree.getCurrentNode();
      var ob2 = this.$refs.tree.getNode(ob);

      if(ob2.isLeaf)
      {
        ob2.loaded = false;
        ob2.expand();
        console.log('调用expand');

      }else{
        this.$refs.tree.append(data, ob);
        console.log('直接增加');
      }

     
      
 
      this.$refs.tree.setCurrentKey(id);
      var ob1 = this.$refs.tree.getNode(data);
    
      this.clickFunc(data, ob1, null);
    },
    addLinAction(id, name) {
      console.log(id, name);

      var data = {
        id: id,
        type: 3,
        name: id + " " + name,
        leaf: true,
      };

      this.$refs.tree.append(data, this.node.childNodes[0]);

      this.$refs.tree.setCurrentKey(id);
      var ob = this.$refs.tree.getNode(data);
      this.clickFunc(data, ob, null);
    },
    dialogOkClick() {
      var that = this;
      var ob = that.$refs.tree.getCurrentNode();

      console.log("删除" + ob.id);
      if (this.dialog.isLine) {
        this.$httpApi.deleteLine(ob.id).then(function (response) {
          if (response.data.code == "0") {
            if (ob) {
              that.$refs.tree.remove(ob);
            }
          } else {
            Message.error(response.data.message);
          }
        });
      } else {
        this.$httpApi.deleteSta(ob.id).then(function (response) {
          if (response.data.code == "0") {
            if (ob) {
              that.$refs.tree.remove(ob);
            }
          } else {
            Message.error(response.data.message);
          }
        });
      }

      this.dialog.visible = false;
    },
    deleteLinAction() {
      var that = this;

      var ob = that.$refs.tree.getCurrentNode();

      this.dialog.tipMsg = "是否删除" + ob.id + "?";
      this.dialog.isLine = true;
      this.dialog.visible = true;
    },
    deleteStation() {
      var that = this;

      var ob = that.$refs.tree.getCurrentNode();

      this.dialog.tipMsg = "是否删除" + ob.id + "?";
      this.dialog.isLine = false;
      this.dialog.visible = true;
    },
    addStation(stat) {
      this.isAdd = true;
      this.sideShow_allLine = false;
      this.sideShow_station = true;
      this.sideShow_line = false;
      this.trsfData = this.currentData;
      this.trsfType = stat;
      console.log(this.trsfData, this.trsfType);
    },
  },
  mounted() {
    let nodedata = this.node.childNodes[0];
    nodedata.expanded = true;
    nodedata.loadData();
  },
};
</script>

<style>
.borderDes {
  border: 1px solid lightgray;
  border-top-style: none;
  border-bottom-style: none;
  border-left-style: none;
}
.fullHeight {
  height: 90vh;
}
.iconSpan {
  margin-left: 2px;
  margin-right: 5px;
}

.box-card {
  width: 200px;
  background-color: lightslategrey;
}
.el-tree--highlight-current .el-tree-node.is-current > .el-tree-node__content {
  background: rgb(112, 144, 235);
  color: #fff;
}

.menu__item {
  display: block;
  line-height: 20px;
  text-align: center;
  margin-top: 10px;
}
.menu {
  width: 150px;
  position: absolute;
  border-radius: 10px;

  border: 1px solid lightgray;
  background-color: #f4f4f4;
}
.treeICon{
  margin-right: 5px;
  margin-left: 5px;
}
</style>