<template>
  <div>

    <div style="margin-right: 10px; margin-left: 10px;">

      <div class="boderDes">
        <h4 style="margin-left: 20px;">{{title}}</h4>

        <el-form
          ref="data"
          :inline="true"
          :disabled='formDisable'
          :model="data"
          label-width="90px"
        >

          <el-form-item
            :label="formTip.id"
            prop="id"
          >
            <el-input
              v-model="data.id"
              :disabled='idDisable'
            ></el-input>
          </el-form-item>
          <el-form-item
            :label="formTip.name"
            prop="name"
          >
            <el-input v-model="data.name"></el-input>
          </el-form-item>

          <el-form-item
            label="启运时间"
            prop="startTime"
          >
            <el-input v-model="data.startTime"></el-input>
          </el-form-item>

          <el-form-item
            label="停运时间"
            prop="stopTime"
          >
            <el-input v-model="data.stopTime"></el-input>
          </el-form-item>

          <el-form-item
            label="英文名称"
            prop="enName"
          >
            <el-input v-model="data.enName"></el-input>
          </el-form-item>

          <el-form-item
            :label="formTip.ip"
            prop="dataServer"
          >
            <el-input v-model="data.dataServer"></el-input>
          </el-form-item>
          <el-form-item
            label="命令服务器"
            prop="cmdServer"
          >
            <el-input v-model="data.cmdServer"></el-input>
          </el-form-item>

          <el-form-item
            label="车站ATS"
            prop="stationAts"
          >
            <el-input v-model="data.stationAts"></el-input>
          </el-form-item>

          <el-form-item
            label="列车ATS"
            prop="trainAts"
          >
            <el-input v-model="data.trainAts"></el-input>
          </el-form-item>

          <el-form-item
            :label="formTip.des"
            prop="des"
          >
            <el-input v-model="data.des"></el-input>
          </el-form-item>

          <el-form-item
            label="是否OCC"
            :formatter="formatOcc"
          >
            <el-checkbox v-model.number="data.center"></el-checkbox>
          </el-form-item>

        </el-form>
      </div>

      <btnGroupVue
        :isAdd="btnIsAdd"
        :showAddBtn="true"
        :okState="okSta"
        @editAction="editClick"
        @cancelAction="cancelClick"
        @okAction="okClick"
        @addAction="addClick"
        @deleteAction="deleteClick"
      >

      </btnGroupVue>

      <addDev
        v-if="showAddDlg"
        :stationCode='addDevData'
        @addFinshed="devAdded"
      ></addDev>

      <div style="margin-top:20px">
        <el-table
          highlight-current-row
          border
          :data="tableData"
          @current-change="handleCurrentChange"
          style="width: 100%"
          max-height="500px"
          ref="tableRef"
          :cell-style="{textAlign:'center'}"
        >

          <el-table-column
            label="设备编号"
            prop="id"
            width="150"
            align="center"
          >
            <template slot-scope="scope">

              <el-input
                class="inputNoBorder"
                v-if="scope.row.isEdit"
                v-model="scope.row.id"
              ></el-input>
              <span v-else>{{scope.row.id}}</span>
            </template>

          </el-table-column>
          <el-table-column
            label="名称"
            prop="name"
            align="center"
          >
            <template slot-scope="scope">

              <el-input
                class="inputNoBorder"
                v-if="scope.row.isEdit"
                v-model="scope.row.name"
              ></el-input>
              <span v-else>{{scope.row.name}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="IP地址"
            prop="ip"
            align="center"
          >
            <template slot-scope="scope">

              <el-input
                class="inputNoBorder"
                v-if="scope.row.isEdit"
                v-model="scope.row.ip"
              ></el-input>
              <span v-else>{{scope.row.ip}}</span>

            </template>
          </el-table-column>
          <el-table-column
            label="物理地址"
            prop="mac"
            align="center"
            width="190"
          >
            <template slot-scope="scope">

              <el-input
                class="inputNoBorder"
                v-if="scope.row.isEdit"
                v-model="scope.row.mac"
              ></el-input>
              <span v-else>{{scope.row.mac}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="类型"
            prop="type"
            width="100"
            align="center"
          >
            <template slot-scope="scope">

              <el-select
                v-model="scope.row.type"
                v-if="scope.row.isEdit"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in typdeList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.name"
                >
                </el-option>
              </el-select>

              <span v-else>{{scope.row.type}}</span>

            </template>
          </el-table-column>

          <el-table-column
            label="播放区域"
            prop="pos"
            width="120"
            align="center"
          >
            <template slot-scope="scope">

              <el-select
                v-model="scope.row.pos"
                v-if="scope.row.isEdit"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in zonlist"
                  :key="item.id"
                  :label="item.name"
                  :value="item.name"
                >
                </el-option>
              </el-select>
              <span v-else>{{scope.row.pos}}</span>

            </template>
          </el-table-column>

          <el-table-column
            label="回传"
            prop="transfer"
            width="70"
            align="center"
          >
            <template slot-scope="scope">

              <el-input
                class="inputNoBorder"
                v-if="scope.row.isEdit"
                v-model="scope.row.transfer"
              ></el-input>
              <span v-else>{{scope.row.transfer}}</span>

            </template>
          </el-table-column>

          <el-table-column
            label="编码号"
            prop="encoder"
            width="70"
            align="center"
          >
            <template slot-scope="scope">

              <el-input
                class="inputNoBorder"
                v-if="scope.row.isEdit"
                v-model="scope.row.encoder"
              ></el-input>
              <span v-else>{{scope.row.encoder}}</span>

            </template>
          </el-table-column>

          <el-table-column
            label="输入号"
            prop="input"
            width="70"
            align="center"
          >
            <template slot-scope="scope">

              <el-input
                class="inputNoBorder"
                v-if="scope.row.isEdit"
                v-model="scope.row.input"
              ></el-input>
              <span v-else>{{scope.row.input}}</span>
            </template>
          </el-table-column>

          <el-table-column
            label="描述"
            prop="des"
            align="center"
          >
            <template slot-scope="scope">

              <el-input
                type="textarea"
                autosize
                class="inputNoBorder"
                v-if="scope.row.isEdit"
                v-model="scope.row.des"
              ></el-input>
              <span v-else>{{scope.row.des}}</span>

            </template>
          </el-table-column>
        </el-table>
      </div>

    </div>

  </div>
</template>

<script>
import { Message } from "element-ui";
import btnGroupVue from "./btnGroup.vue";
import addDev from "./addDev.vue";
export default {
  components: {
    btnGroupVue,
    addDev,
  },
  props: {
    code: String,
    isStation: Boolean,
    isAdd: Boolean,
  },
  data() {
    return {
      btnIsAdd: false,
      formDisable: true,
      data: {},
      tableData: [],
      formTip: {},
      currentFormData: {},
      currentTableData: [],
      okSta: false,
      isOcc: false,
      title: "车站信息",
      idDisable: true,
      addDevData: {},
      showAddDlg: false,
      currentRow: {},
      zonlist: [],
      typdeList: [],
    };
  },
  watch: {
    data: {
      handler(newvale) {
        this.okSta = this.dataIsEqual(newvale, this.currentFormData, false);
        console.log("表单变化", this.okSta);
      },
      deep: true,
    },
    tableData: {
      handler(newvale) {
        this.okSta = this.dataIsEqual(newvale, this.currentTableData, true);
        console.log("列表变化", this.okSta);
      },
      deep: true,
    },
    isAdd() {
      this.updateData();
    },
    code() {
      this.updateData();
    },
    isStation() {
      this.updateData();
    },
    formDisable(newVal) {
      if (newVal) {
        this.tableData.forEach((item) => {
          item.isEdit = false;
        });
      }
    },
  },
  methods: {
    dataIsEqual(newCurr, newJs, isDev) {
      var nwe, cur;

      if (isDev) {
        var newJs22 =
          newJs == undefined ? [] : JSON.parse(JSON.stringify(newJs));
        var newCurr22 =
          newCurr == undefined ? [] : JSON.parse(JSON.stringify(newCurr));

        if (newJs != undefined) {
          newJs22.forEach((element) => {
            delete element.isEdit;
          });
        }
        if (newCurr != undefined) {
          newCurr22.forEach((element) => {
            delete element.isEdit;
          });
        }

        nwe = JSON.stringify(newJs22);
        cur = JSON.stringify(newCurr22);
      } else {
        nwe = JSON.stringify(newJs);
        cur = JSON.stringify(newCurr);
      }

      return nwe === cur;
    },

    formatOcc(row, column, cellValue) {
      var ret = "";
      if (cellValue) {
        this.isOcc = true;
      } else {
        this.isOcc = false;
      }
      return ret;
    },

    editClick(callbck) {
      this.formDisable = false;
      if (this.currentRow) {
        this.currentRow.isEdit = true;
      }
      callbck();
    },
    cancelClick(callbck) {
      this.formDisable = true;
      this.data = JSON.parse(JSON.stringify(this.currentFormData));
      this.tableData = JSON.parse(JSON.stringify(this.currentTableData));
      callbck();
    },
    addClick() {
      var da = {
        id: this.data.id + "-",
        mac: "00-00-00-00-00-00",
      };
      if (this.tableData == undefined) {
        this.tableData = new Array();
      }
      this.tableData.push(da);
    },

    handleCurrentChange(val) {
      console.log("选中 ", JSON.stringify(val));
      this.currentRow = val;
      //是否可编辑
      if (this.formDisable) {
        this.tableData.forEach((item) => {
          item.isEdit = false;
        });
        return;
      }
      if (val) {
        this.tableData.forEach((item) => {
          if (item.id == val.id) {
            item.isEdit = true;
          } else {
            item.isEdit = false;
          }
        });
      }
    },
    deleteClick() {
      if (this.currentRow.id == undefined) {
        Message.error("未选中任何行数据");
        return;
      }

      this.tableData.forEach((element, index) => {
        if (element.id == this.currentRow.id) {
          this.tableData.splice(index, 1);
          return;
        }
      });
    },
    addStationOk() {
      var that = this;
      console.log("添加站点");
      that.data.isTrain = !this.isStation;
      that.data.Line_Code = this.code;
      this.$httpApi.addStation(that.data).then(function (response) {
        if (response.data.code == "0") {
          that.okSta = true;
          that.currentFormData = JSON.parse(JSON.stringify(that.data));
          that.formDisable = true;

          that.$emit("add", that.data.id, that.data.name, !that.isStation);
          return true;
        } else {
          Message.error(response.data.message);
          // that.data = JSON.parse(JSON.stringify(that.currentFormData));
        }
      });

      for (let index = 0; index < this.tableData.length; index++) {
        const element = this.tableData[index];

      

        var postData = JSON.parse(JSON.stringify(element));

        postData.station_code = that.data.id;
        postData.line_code = that.code;
        that.typdeList.forEach((element) => {
          if (postData.type == element.name) {
            postData.type = element.id;
          }
        });
        console.log("增加", JSON.stringify(postData));
        this.$httpApi.addDev(postData).then(function (response) {
          if (response.data.code == "0") {
            console.log("增加成功", postData.id);
          } else {
            Message.error(response.data.message);
          }
        });
      }

      return false;
    },
    okClick(callbck) {
      var that = this;
      console.log(JSON.stringify(this.data));

      if (this.isAdd) {
        var suc = this.addStationOk();
        if (suc) {
          callbck();
        }
        return;
      }

      //判断信息是否发生变化

      if (!this.dataIsEqual(this.data, this.currentFormData)) {
        console.log("更新站点信息");
        this.$httpApi
          .updateStationInfo(this.code, that.data)
          .then(function (response) {
            if (response.data.code == "0") {
              that.currentFormData = JSON.parse(JSON.stringify(that.data));
              that.$emit("change", that.data.id, that.data.name);
            } else {
              that.data = JSON.parse(JSON.stringify(that.currentFormData));
            }
          });
      }

      //判断是否增加了
      if (this.currentTableData == undefined) {
        console.log("new curr table");
        this.currentTableData = new Array();
      }

      if (this.tableData.length > this.currentTableData.length) {
        for (let index = 0; index < this.tableData.length; index++) {
          const element = this.tableData[index];
          var bHavEl1 = false;
          for (
            let index1 = 0;
            index1 < this.currentTableData.length;
            index1++
          ) {
            const element1 = this.currentTableData[index1];
            if (element.id == element1.id) {
              bHavEl1 = true;
              break;
            }
          }
          if (!bHavEl1) {
            //

            var postData = JSON.parse(JSON.stringify(element));

            postData.station_code = this.data.id;
            postData.line_code = this.data.lineCode;
            that.typdeList.forEach((element) => {
              if (postData.type == element.name) {
                postData.type = element.id;
              }
            });
            console.log("增加", JSON.stringify(postData));
            this.$httpApi.addDev(postData).then(function (response) {
              if (response.data.code == "0") {
                console.log("增加成功", postData.id);
              } else {
                Message.error(response.data.message);
              }
            });
          }
        }
      } else {
        //判断 是否 发生了更改 和 删除了
        for (let index = 0; index < this.currentTableData.length; index++) {
          const element = this.currentTableData[index];
          var bHavEl = false;
          for (let index1 = 0; index1 < this.tableData.length; index1++) {
            const element1 = this.tableData[index1];
            if (element.id == element1.id) {
              console.log("zaaa");
              bHavEl = true;
              var nv1 = JSON.parse(JSON.stringify(element));
              var nv2 = JSON.parse(JSON.stringify(element1));
              delete nv2.isEdit;
              delete nv1.isEdit;
              if (JSON.stringify(nv1) != JSON.stringify(nv2)) {
                //更改了

                let postData1 = JSON.parse(JSON.stringify(nv2));

                postData1.station_code = this.data.id;
                postData1.line_code = this.data.lineCode;
                that.typdeList.forEach((element) => {
                  if (postData1.type == element.name) {
                    postData1.type = element.id;
                  }
                });
                console.log("更改", JSON.stringify(postData1));

                this.$httpApi.upDataDev(postData1).then(function (response) {
                  if (response.data.code == "0") {
                    console.log("更改成功", postData1.id);
                  } else {
                    Message.error(response.data.message);
                  }
                });
              }
              break;
            }
          }

          if (!bHavEl) {
            // 被删除了
            console.log("删除", element.id);
            this.$httpApi.deleteDev(element.id).then(function (response) {
              if (response.data.code == "0") {
                console.log("删除成功", element.id);
              } else {
                Message.error(response.data.message);
              }
            });
          }
        }
      }

      that.currentTableData = JSON.parse(JSON.stringify(that.tableData));
      this.formDisable = true;
      that.okSta = true;
      this.currentFormData = JSON.parse(JSON.stringify(this.data));

      callbck();
    },
    initAddSta() {
      this.btnIsAdd = true;
      this.title = this.isStation ? "增加车站信息" : "增加列车信息";
      this.formDisable = false;
      this.idDisable = false;
      this.data = {
        center: false,
        cmdServer: "",
        dataServer: "",
        des: "",
        enName: "",
        id: this.isStation ? "S" : "T",
        name: "",
        startTime: "00:00:00",
        stationAts: "",
        stopTime: "00:00:00",
        trainAts: "",
      };
      this.currentFormData = JSON.parse(JSON.stringify(this.data));
      this.tableData = [];
      this.currentTableData = [];
      console.log("添加");
    },
    updateData() {
      console.log("获取车站信息" + this.code);

      this.formTip.id = this.isStation ? "车站编号" : "列车编号";
      this.formTip.name = this.isStation ? "车站名称" : "列车名称";
      this.formTip.des = this.isStation ? "车站描述" : "列车描述";
      this.formTip.ip = this.isStation ? "服务器IP" : "直播服务";

      if (this.isAdd) {
        this.initAddSta();
        return;
      }
      const that = this;
      this.btnIsAdd = false;
      this.title = this.isStation ? "车站信息" : "列车信息";
      this.idDisable = true;

      this.$httpApi.stationInfo(this.code).then(function (response) {
        if (response.data.code == "0") {
          //that.treeData=response.data.dataList;
          console.log(JSON.stringify(response.data));
          that.data = response.data.info;
          that.tableData = response.data.devList;
          if (that.tableData != undefined) {
            that.tableData.forEach((item) => {
              item.isEdit = false;
              that.typdeList.forEach((element) => {
                if (element.id == item.type) {
                  item.type = element.name;
                }
              });
            });
            that.currentTableData = JSON.parse(JSON.stringify(that.tableData));
          } else {
            that.currentTableData = new Array;
             that.tableData = new Array
          }
         
          that.currentFormData = JSON.parse(JSON.stringify(that.data));
        }
      });
    },
  },
  async mounted() {
    const that = this;
    console.log("获取zoneAndtyoe");
    await this.$httpApi.zoneList_get_sync().then(function (response) {
      if (response.data.code == "0") {
        that.zonlist = response.data.zoneList;
        console.log("zone success");
      }
    });

    await this.$httpApi.typdeList_sync().then(function (response) {
      if (response.data.code == "0") {
        that.typdeList = response.data.dataList;
        console.log("type success");
      }
    });
    console.log("请求完成");

    this.updateData();
  },
};
</script>

<style scoped>
.boderDes {
  border: 1px solid lightgray;

  margin-bottom: 20px;
}
.inputNoBorder {
  widows: 100%;
  height: 100%;
}
.inputNoBorder >>> .el-input__inner {
  -webkit-appearance: none;
  background-color: transparent;
  background-image: none;
  border-radius: 4px;
  border: 0px;
  width: 100%;
  text-align: center;
}
.inputNoBorder >>> .el-textarea__inner {
  border: 0;
  resize: none; /* 这个是去掉 textarea 下面拉伸的那个标志，如下图 */
}
</style>