<template>
  <div>

    <div style="margin-right: 20px; margin-left: 20px;">

      <div >
        <el-table
          highlight-current-row
          border
          :data="tableData_type"
          style="width: 100%"
          
          max-height="500px"
        >

          <el-table-column
            label="线路编号"
            prop="id"
          >
          </el-table-column>
          <el-table-column
            label="线路名称"
            prop="name"
          >
          </el-table-column>
          <el-table-column
            label="英文名称"
            prop="enName"
          >
          </el-table-column>
          <el-table-column
            label="数据服务器"
            prop="dataServer"
          >
          </el-table-column>
           <el-table-column
            label="命令服务器"
            prop="cmdServer"
          >
          </el-table-column>
          <el-table-column
            label="支线路数"
            prop="subLineNum"
          >
          </el-table-column>
        </el-table>

      </div>

    </div>

  </div>
</template>

<script>
export default {
  name: "allLineVue",
  data(){

      return {
          tableData_type:[]
      }

  },
  mounted(){
    
    console.log('获取全部线路信息');
    var that = this;
    this.$httpApi.lineInfo('0').then(function (response) {
          if (response.data.code == "0") {
            //that.treeData=response.data.dataList;
            console.log(JSON.stringify(response.data.dataList));
            that.tableData_type= response.data.dataList;
          }
        });
  }
};
</script>

