<template>
  <div>

    <el-dialog
      :title="formData.tipMsg"
      :visible.sync="dialogFormVisible"
    >
      <el-form :model="formData">
        <el-form-item
          label="活动名称"
          label-width="120px"
        >
          <el-input
            v-model="formData.name"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="dialogFormVisible = false"
        >确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
export default {
  props: {
    stationCode: {},
  },
  watch: {
    stationCode: {
      handler() {
        this.upData();
      },
      deep: true
    },
  },
  methods: {
    upData() {
        this.dialogFormVisible = true;
        this.formData.tipMsg = '增加设备(编号格式:)' + this.stationCode.isStation ? 'S':'T' + 'xxss-xx)(编码号为255表示设备自身编码回传)';
        this.formData.stationCode = this.stationCode.id;
        this.formData.devCode='';
        this.formData.devName='';
        this.formData.devIp='';
        this.formData.devMac='00-00-00-00-00-00';
        this.formData.devType='';
        this.formData.devZone='';
        this.formData.devBackTrs='';
        this.formData.devEncoderNum='';
        this.formData.devInputNum='';
        this.formData.devDes='';


    },
  },
  data() {
    return {

        formData:{

        },
        dialogFormVisible:false

    };
  },
};
</script>

