<template>
  <div>
    <el-button
      type="primary"
      icon="el-icon-edit"
      :disabled="btnStatue.edit"
      @click="editClick()"
    >
      编 辑
    </el-button>

    <el-button
      type="success"
      icon="el-icon-plus"
      v-if="showAddBtn"
      @click="addClick"
       :disabled="btnStatue.add"
    >
      添加设备
    </el-button>
    <el-button
      type="warning"
      icon="el-icon-delete"
      v-if="showAddBtn"
      @click="deleteClick"
       :disabled="btnStatue.delete"
    >
      删除设备
    </el-button>

    <el-button
      :disabled="btnStatue.cancel"
      @click="cancelClick()"
    >
      取消
    </el-button>

    <el-button
      type="primary"
      :disabled="btnStatue.ok"
      @click="okClick()"
    >
      确认
    </el-button>

  </div>
</template>

<script>
export default {
  props: {
    showAddBtn: Boolean,
    okState:Boolean,
    isAdd:Boolean
  },
  watch:{
    okState(newV){
      console.log('btn',newV);
      this.btnStatue.ok = newV;
    },
    isAdd(newV)
    {
      console.log('btn ' + newV);
      this.btnStatue.cancel = !newV;
      this.btnStatue.edit = newV;
      this.btnStatue.add = this.btnStatue.delete = !newV;
    }
  },
  data() {
    return {
      btnStatue: {
        edit: false,
        add: true,
        delete: true,
        cancel: true,
        ok: true,
      },
    };
  },
  methods: {
    addClick() {
      this.$emit("addAction");
    },
    deleteClick() {
      this.$emit("deleteAction");
    },
    editClick() {
      this.$emit("editAction", () => {
        this.btnStatue.cancel = false;
        this.btnStatue.edit = true;
        this.btnStatue.add = this.btnStatue.delete = false;
      });
    },
    cancelClick() {
      this.$emit("cancelAction", () => {
        this.btnStatue.cancel = true;
        this.btnStatue.ok = true;
        this.btnStatue.edit = false;
        this.btnStatue.add = this.btnStatue.delete = true;
      });
    },
    okClick() {
      this.$emit("okAction", () => {
        
        this.btnStatue.cancel = true;
        this.btnStatue.ok = true;
        this.btnStatue.edit = false;
        this.btnStatue.add = this.btnStatue.delete = true;
      });
    },
  },
};
</script>

